
/* Box */
/* --------------------------------------------------- */

.box							{ border-left: 4px solid #9ea7b3; line-height: 1.5em; position: relative; }

.box .part						{ padding: 1.5em; }
.box .part.shaded				{ background: rgba(0, 0, 0, 0.2); border-width: 1px 0; border-style: solid; border-color: #ddd; }

.box .part.divideleft			{ border-left: 1px solid rgba(0, 0, 0, 0.2); }
.box .part.divideright			{ border-right: 1px solid rgba(0, 0, 0, 0.2); }

/* Tool Bar */
.box .toolbar					{ background: #333; font-size: 1.3rem; line-height: 3.4rem; border-radius: 0 0 2px 2px; overflow: hidden; clear:both; }
.box .toolbar ul,	
.box .toolbar li				{ float: left; }

.box .toolbar a,
.box .toolbar button			{ display: block; height: 3.4rem; background: transparent; font-size: 1.3rem; padding: 0 16px; text-decoration: none; border-width: 0 1px 0 0; border-style: solid; border-color: #000; cursor: pointer; }

.box .toolbar a:hover,
.box .toolbar button:hover		{ color: #fff; }

.box .toolbar ul.right			{ float: right; }

.box .toolbar ul.right a,
.box .toolbar ul.right button	{ border-width: 0 0 0 1px; border-color: #126c96; font-weight: 600; background: #2A8FBD; color: #fff; font-size: 1.4rem; }
/*.box .toolbar ul.right a.deleteButton { background-color: #2c6d8a; }*/

.box .toolbar ul.right a:hover,
.box .toolbar ul.right button:hover	{ background: #1e80aa; }

.task .toolbar .selected,
.task .toolbar a:active			{ background: #000; }

/* No Touch */
.box .notouch					{ background: rgba(0, 0, 0, 0.5); color: #fff; font-size: 4.2rem; text-align: center; position: absolute; top: 0; right: 0; bottom: 0; left: 0; z-index: 2000; }
.box .notouch p					{ position: absolute; top: 40%; width: 100%; background: rgba(0, 0, 0, 0.5);}


/* Project */
/* --------------------------------------------------- */

.project						{ font-size: 1.6rem; max-width: 45em; margin: 0 auto; }
.project h1						{ font-size: 1.4em; margin: 0 0 1em; }

.project .details				{ float: left; width: 70%; }

.project .info					{ color: #888; }

.project span.noTasksAvailable	{ font-style: italic; color: #aaa; }

.project .meta					{ float: left; width: 30%; font-size: 1.4rem; }
.project .meta li				{ border-bottom: 1px solid #ddd; padding: 1em; }
.project .meta li:first-child	{ padding-top: 0; }
.project .meta li:last-child	{ padding-bottom: 0; border-bottom: 0; }
.project .meta p				{ margin: 0; }

.project .guidelines h2			{ font-size: 1.8rem; text-transform: uppercase; letter-spacing: 1px; color: #444; margin: 0 0 1.5em; padding: 0 0 0.65em; border-bottom: 1px solid #bbb; }

.project .percentbar			{ border: 1px solid #ccc; border-radius: 3px; width: 300px; display: block; padding: 1px; margin: 0 0 3em; line-height: 1em; }
.project .percentfill			{ background: #ccc; border-radius: 2px; display: block; position: relative; height: 8px; }
.project .percentnum			{ position: absolute; bottom: -18px; right: 0; font-size: 11px; }



.projects .project	{ font-size: 1.3rem; padding: 1.3em; margin: 0 0 2rem; text-align: left; max-width: none; }
.projects .project h1{ white-space: nowrap; overflow: hidden; text-overflow: ellipsis; color: #999; }
.projects .project .description { max-width: 500px; }
.projects .project .button { position: absolute; top: 2rem; right: 2rem; }



/* Forms */
/* --------------------------------------------------- */

form .field						{ margin: 0 0 1.5em; }
form label						{ display: block; font-weight: 600; font-size: 1.4rem; margin: 0 0 3px; }

form input[type="text"],
form #transcriptionTextArea			{ font-family: inherit; font-size: 1.3rem; padding: 8px; width: 100%; border: 1px solid #bbb; border-radius: 3px; box-shadow: inset 0 1px 3px #ddd; margin: 0; display: block; }

form .option					{ display: inline-block; margin: 0 0.3rem 0.5rem;  background: #ddd; padding: 0.2em 0.4em; line-height: 1em; border-radius: 3px; }
form .option label 				{ display: inline; font-weight: normal; font-size: 1.2rem; }

form button,
form input[type="submit"]		{ -webkit-appearance: none; }
#saveButton.saved {}
#saveButton.saving { background-color: yellow; }
#saveButton.failedToSave { background-color: red; }

