/* Tasks */
/* --------------------------------------------------- */

.task .source,
.task .transcript				{ height: -webkit-calc(100% - 3.4rem); height: calc(100% - 3.4rem); }

/* Source */
.task .source					{ width: 65%; float: left; background: #555; }
.task .source figure			{ overflow-y: scroll; height: 100%; }
.task .source img				{ display: block; width: 100%; }

/* Transcript */
.task .transcript				{ width: 35%; float: right; position: relative; border-width: 0 0 0 1px; border-style: solid; border-color: #ccc; }
.task .transcript #transcriptionTextArea		{ height: 100%; padding: 2rem; font-size: 1.8rem; outline: none; border-radius: 0; border: none; box-shadow: none;}

/* With Tags */
.task .transcript .tags			{ display: none; background: #eee; padding: 1rem 2rem; height: 140px; border-width: 1px 0 0; border-style: solid; border-color: #ccc; overflow: auto; }
.task .transcript .tags button	{ background: #777; border: none; color: #fff; border-radius: 3px; padding: 0.3rem 0.7rem 0.5rem; font-size: 1.1rem; }
.task .transcript .tags button:hover	{ background: #555; cursor: pointer }

.task .transcript.has_tags #transcriptionTextArea	{ height: -webkit-calc(100% - 140px); height: calc(100% - 140px); }
.task .transcript.has_tags .tags	{ display: block; }

/* Staked View */
.task.stacked .source			{ width: 100%; height: 100%; float: none; height: 60%; }
.task.stacked .transcript		{ width: 100%; float: none; height: -webkit-calc(40% - 3.4rem); height: calc(40% - 3.4rem); border-width: 1px 0 0 0; }

.task.stacked .transcript.has_tags #transcriptionTextArea	{ width: -webkit-calc(100% - 240px); width: calc(100% - 240px); height: 100%; float: left; }
.task.stacked .transcript.has_tags .tags	{ width: 240px; height: 100%; display: block; float: right; border-width: 0 0 0 1px; }

.task .toolbar #sidebyside,
.task .toolbar #stacked			{ text-indent: -9999em; width: 36px; background-repeat: no-repeat; background-position: center center; opacity: 0.8; }
 
.task .toolbar #sidebyside		{ background-image: url('../img/sidebyside_icon_blue.png'); }
.task .toolbar #stacked			{ background-image: url('../img/stacked_icon_blue.png'); }

.task .toolbar #sidebyside:hover,
.task .toolbar #sidebyside.selected { background-image: url('../img/sidebyside_icon_white.png'); }

.task .toolbar #stacked:hover,
.task .toolbar #stacked.selected	{ background-image: url('../img/stacked_icon_white.png'); }
